/* Hambuger styling */
.NavBar_hamburger__2R-IL {
    z-index: 100;
}

.NavBar_label__jzfAt {
    display:flex;
    flex-direction:column;
    width: 40px;
    height: 40px;
    cursor:pointer;
}

.NavBar_label__jzfAt span{
    background: black;
    border-radius: 10px;
    height: 4px;
    margin: 4px 0;
    -webkit-transition: .4s  cubic-bezier(0.68, -0.6, 0.32, 1.6);
    transition: .4s  cubic-bezier(0.68, -0.6, 0.32, 1.6);
}


span:nth-of-type(1){
    width: 50%;
    
}

span:nth-of-type(2){
    width: 100%;
}


span:nth-of-type(3){
    width: 75%;

}


input[type="checkbox"]{
    display: none;
}


input[type="checkbox"]:checked ~ span:nth-of-type(1){
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    -webkit-transform: rotatez(45deg) translate(4.6px,0px);
            transform: rotatez(45deg) translate(4.6px,0px)
}


input[type="checkbox"]:checked ~ span:nth-of-type(2){
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transform: rotatez(-45deg);
            transform: rotatez(-45deg)
}


input[type="checkbox"]:checked ~ span:nth-of-type(3){
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    width: 50%;
    -webkit-transform: translate(17.1px,-6.3px) rotatez(45deg);
            transform: translate(17.1px,-6.3px) rotatez(45deg);
}   

.NavBar_mobileNavMenu__VOGbe {
    position: absolute;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    overflow: hidden;
    background-color: #CEAF7F;
    margin: 0;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-transition: left 0.3s ease-out;
    transition: left 0.3s ease-out;
}

.NavBar_mobileNavMenu__VOGbe ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: block;
}

.NavBar_mobileNavMenu__VOGbe ul li {
    display: block;
    padding: 50px 0;
}

.NavBar_mobileNavMenu__VOGbe ul li a {
    font-size: 2rem;
    padding: 8px 8px;
    position: relative;
}

.NavBar_mobileNavMenu__VOGbe ul li a:nth-child(1) {
    -webkit-transition: opacity 0.3s ease-out;
    transition: opacity 0.3s ease-out;
}

.NavBar_mobileNavMenu__VOGbe ul li a:nth-child(2) {
    -webkit-transition: opacity 0.5s ease-out;
    transition: opacity 0.5s ease-out;
}

.NavBar_mobileNavMenu__VOGbe ul li a:nth-child(3) {
    -webkit-transition: opacity 0.7s ease-out;
    transition: opacity 0.7s ease-out;
}

.NavBar_navLink__ZNM9n {
    color: black;
    text-decoration: none;
}

.NavBar_navLink__ZNM9n:hover {
    -webkit-text-decoration: underline #CEAF7F 3px;
